.main-info-container {
    color: green;
    font-weight: bolder;
    font-size: 1em;
    margin-bottom: 50px;
}

.main-info-element {
    display: block;
    margin: 5px;
}

.main-info-container span {
    overflow: hidden;
    border-right: .15em solid green;
    padding-right: 20px;
    white-space: nowrap;
    margin: 0 auto;
    letter-spacing: .25em;
    animation: 
      typing 3.5s steps(40, end),
      blink-caret .75s step-end infinite;
  }
  
  /* The typing effect */
  @keyframes typing {
    from { width: 0 }
    to { width: 100% }
  }
  
  /* The typewriter cursor effect */
  @keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color: green; }
  }